/* Estilos para la sección HERO principal */
.hero {
  height: 100vh; /* Ocupa el 100% de la altura de la pantalla */
  background-color: #1b1b1b; /* Color de fondo oscuro (como en tu página) */
  color: white; /* Texto blanco */
  display: flex; /* Usamos flexbox para centrar */
  justify-content: center; /* Centra horizontalmente */
  align-items: center; /* Centra verticalmente */
  text-align: center; /* Centra el texto */
  padding: 20px; /* Espaciado interno */
}

/* Contenedor del contenido del hero */
.hero-content {
  max-width: 800px; /* Ancho máximo del contenido */
}

/* Estilo para el título */
.hero-title {
  font-size: 4rem; /* Tamaño grande para destacar */
  margin-bottom: 1rem; /* Espacio debajo del título */
  font-weight: bold; /* Texto en negrita */
}

/* Estilo para el subtítulo o descripción */
.hero-subtitle {
  font-size: 1.5rem; /* Un poco más pequeño que el título */
  margin-bottom: 2rem; /* Espacio antes del botón */
}

/* Estilo para el botón de acción */
.hero-button {
  padding: 0.75rem 2rem; /* Espaciado interno del botón */
  font-size: 1rem; /* Tamaño del texto */
  background-color: #007bff; /* Color azul */
  color: white; /* Texto blanco */
  border: none; /* Sin borde */
  border-radius: 5px; /* Bordes redondeados */
  cursor: pointer; /* Cursor tipo puntero */
  transition: background-color 0.3s ease; /* Animación suave */
}

/* Efecto hover para el botón */
.hero-button:hover {
  background-color: #0056b3; /* Azul más oscuro al pasar el mouse */
}
